import React from "react";
import s from "./header.module.scss";
import {GridRow} from "../../lib-ui";
import icons from "../../media/icons.svg";
import Menu from "./menu";
import {NavLink} from "react-router-dom";
import navLink from "../../navigation";


const Header = () => {
    return <header>
        <GridRow className={s.bg} justify={"between"}>
            <NavLink to={navLink.homepage}> <svg className={s.logo}>
                <use href={icons + "#logo"} xlinkHref={icons + "#logo"}/>
                </svg>
            </NavLink>
            <div>
                <p>Официальные дилеры</p>
            </div>
        </GridRow>
        <GridRow justify={"left"}>
            <Menu/>
        </GridRow>
    </header>
};

export default Header;
