import {Button, GridItem} from "../../lib-ui";
import React from "react";
import s from "./bikes-item.module.scss";
import PropTypes from 'prop-types';
import navLink from "../../navigation";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import {chooseBike} from "../../redux/actions/app";

const BikesItem = ({info = undefined}) => {

    const dispatch = useDispatch();
    const history = useHistory();

    const openBike = (info) => {
        dispatch( chooseBike(info) );
        history.push(`${navLink.productsBikes}/${info.id}`);
    };

    return <GridItem width={3} align={'stretch'} className={s.all}>
        <div className={s.img}>
            {info && <img src={info.img} alt={info.title} />}
        </div>
        {info ? <h2>{info.name}</h2> : <h2>loading..</h2>}
        {info && <Button onClick={() => openBike(info)}>Подробнее</Button>}
    </GridItem>

};
export default BikesItem;

BikesItem.propTypes = {
    info: PropTypes.exact({
        id: PropTypes.string.isRequired,
        name: PropTypes.string.isRequired,
        img: PropTypes.string.isRequired,
    })
};