import {bikeList, bikes} from "./fake-server";

export default class API {
    host = "";

    getResource = async (value) => {
        return new Promise(resolve => setTimeout(resolve, 1000, value));
    };

    getBikesList = async () => {
        const response = await this.getResource(bikeList);
        return response;
    };
    getBike = async (id) => {
        const response = await this.getResource(bikes.find((el) => el.id === id));
        debugger
        return response;
    };
};