import {CHOOSE_BIKE, GET_BIKE, UPDATE_BIKES_LIST} from "../constant";

export const updateBikesList = (items) => {
    return {
        type: UPDATE_BIKES_LIST,
        value: items
    };
};

export const chooseBike = (items) => {
    return {
        type: CHOOSE_BIKE,
        value: items
    };
};

export const getBike = (items) => {
    return {
        type: GET_BIKE,
        value: items
    }
}