import React from "react";
import s from "./grid-col.module.scss";
import {buildStyle} from "../settings/settings";

const GridItem = ({children, className, width, align, spacing, ...rest}) => {

    let style = [];

    if (width < 13 && width > 0) {
        style.push(s["width_" + width]);
    }

    switch(align) {
        case "middle":
            style.push(s.align_middle);
            break;
        case "bottom":
            style.push(s.align_bottom);
            break;
        case "top":
            style.push(s.align_top);
            break;
        default:
            break;
    }

    if (spacing) {
        style.push(s['spacing_' + spacing]);
    }


    return <div {...rest} className={buildStyle(style)}>
        <div className={buildStyle([s.inner, className])}>
            {children}
        </div>
    </div>
};

export default GridItem;
