import React, {Component} from "react";
import { GridRow} from "../../lib-ui";
import API from "../../service/api";
import BikesItem from "./bikes-item";
import s from './page-bikes.module.scss';
import {connect} from "react-redux";
import {updateBikesList} from "../../redux/actions/app";
import {Helmet} from "react-helmet";


class PageBikes extends Component {
    api = new API();

    async componentDidMount() {
        let bikeList = await this.api.getBikesList();
        this.props.updateBikesList(bikeList);
    }

    helmet = <Helmet>
        <title>Страница товаров великов</title>
        <meta name="description" content="страница великов" />
    </Helmet>;

    render () {

        if (this.props.bikesList) {
            return <>{this.helmet}<GridRow className={s.all}>
                {this.props.bikesList.map((el) => {
                    return <BikesItem key={el.id} info={el}/>
                })}
            </GridRow></>
        } else {
            return <>{this.helmet}<GridRow className={s.all}>
                <BikesItem />
                <BikesItem />
                <BikesItem />
            </GridRow></>}
        }


}

const mapDispatchToProps = (state) => {
    return {
        bikesList: state.app.bikesList
    };
}


export default connect(mapDispatchToProps, {updateBikesList})(PageBikes);