import React from "react";
import s from './page-home.module.scss'
import {Button, GridItem, GridRow} from "../../lib-ui";
import {NavLink} from "react-router-dom";
import navLink from "../../navigation";
import {Helmet} from "react-helmet";

const Homepage = () => {
    return <div>
        <Helmet>
            <title>Главная страница</title>
            <meta name="description" content="Главная страница великов" />
        </Helmet>
        <GridRow className={s.firstBlock}>
            <h1 className={s.title}>SHUSTER</h1>
        </GridRow>
        <div className={s.secondBlock}>
            <GridRow justify={"center"}>
                <GridItem width={5} className={s.secondBlock_img_left}>
                    <img src={require('../../media/shusterL78688.jpg').default} alt={""}/>
                </GridItem>
                <GridItem width={5} className={s.secondBlock_name_right}>
                    <h2 className={s.subTitle}>Велосипеды</h2>
                    <NavLink to={navLink.productsBikes}>
                        <Button>Подробнее</Button>
                    </NavLink>
                </GridItem>

            </GridRow>

            <GridRow >

                <GridItem width={5} className={s.secondBlock_name_left}>
                    <h2 className={s.subTitle}>Одежда</h2>
                    <NavLink to={navLink.productsCloths}>
                        <Button>Подробнее</Button>
                    </NavLink>
                </GridItem>
                <GridItem width={5} className={s.secondBlock_img_right}>
                    <img src={require('../../media/bieganie_1.png').default} alt={""}/>
                </GridItem>

            </GridRow>

            <GridRow justify={"center"}>
                <GridItem width={5} className={s.secondBlock_img_left}>
                    <img src={require('../../media/bieganie_2.png').default} alt={""}/>
                </GridItem>
                <GridItem width={5} className={s.secondBlock_name_right}>
                    <h2 className={s.subTitle}>Обувь</h2>
                    <NavLink to={navLink.productsShoes}>
                        <Button>Подробнее</Button>
                    </NavLink>
                </GridItem>

            </GridRow>

        </div>
    </div>;
};

export default Homepage;
