import {CHOOSE_BIKE, GET_BIKE, UPDATE_BIKES_LIST} from "../constant";
import PropTypes from "prop-types";

const initialState = {
    bikesList: undefined,
    chosenBike: undefined
};

export default function app(state = initialState, action) {

    switch (action.type) {
        case UPDATE_BIKES_LIST:
            return {
                ...state,
                bikesList: action.value
            };

        case CHOOSE_BIKE:
            return {
                ...state,
                chosenBike: {
                    id: action.value.id,
                    name: action.value.name
                }
            };

        case GET_BIKE:
            return {
                ...state,
                chosenBike: {
                    fromServer: true,
                    id: action.value.id,
                    name: action.value.name,
                    tech: action.value.tech,
                }
            };

        default:
            return state;
    }
}