import React, {Component} from "react";
import PropTypes from 'prop-types';
import API from "../../service/api";
import {connect} from "react-redux";
import {getBike} from "../../redux/actions/app";
import {Helmet} from "react-helmet";

class PageBike extends Component {
    api = new API();

    async componentDidMount() {
        let bikeList = await this.api.getBike(this.props.id);
        this.props.getBike(bikeList);
    }


    render() {
        if (!this.props.chosenBike) {
            //TODO переделать с ответом от сервера
           return null;
        }
        return <>
            <Helmet>
                <title>{this.props.chosenBike.name}</title>
                <meta name="description" content={`велосипед ${this.props.chosenBike.name}`} />
            </Helmet>
            <h1 className={this.props.titleClass}>{this.props.chosenBike.name}</h1>
        <p>{this.props.id}</p>
            {this.props.chosenBike.tech &&
                <div>
                    <p>Высота: {this.props.chosenBike.tech.width}</p>
                    <p>Длина: {this.props.chosenBike.tech.height}</p>
                    <p>Вес: {this.props.chosenBike.tech.weight}</p></div> }
    </>}

}

const mapStateToProps = (state) => {
    return {
        chosenBike: state.app.chosenBike,
    }
};

export default connect(mapStateToProps, {getBike})(PageBike);

PageBike.propTypes = {
    titleClass: PropTypes.string.isRequired,
    id: PropTypes.string.isRequired,
    chosenBike: PropTypes.shape({
        name: PropTypes.string.isRequired,
        tech: PropTypes.object,
    }),
}