import React from "react";
import Header from "../header/header";
import {Redirect, Route, Switch} from "react-router-dom";
import navLink from "../../navigation";
import Homepage from "../page-home/page-home";
import Footer from "../footer/footer";
import PageBikes from "../page-bikes/page-bikes";
import s from './app.module.scss'
import PageBike from "../page-bike/page-bike";


function App() {
  return (
    <div>
      <Header/>
      <Switch>
        <Route exact path={navLink.homepage}>
          <Homepage/>
        </Route>
        <Route path={navLink.productsBikes} exact>
          <h1 className={s.title}>Велосипеды</h1>
          <PageBikes/>
        </Route>
        <Route path={`${navLink.productsBikes}/:id`}
               render={({ match }) => {
                 const {id} = match.params;
                 return  <PageBike id={id} titleClass={s.title}/>
               }} />
        <Route path={navLink.productsCloths} exact>
          <h1 className={s.title}>Одежда</h1>
          <p>products Cloths !!</p>
        </Route>
        <Route path={navLink.productsShoes} exact>
          <h1 className={s.title}>Обувь</h1>
          <p>products SHOES!!</p>
        </Route>
        <Route path={navLink.contacts} exact>
          <h1 className={s.title}>Контакты</h1>
          <p>contacts !!</p>
        </Route>
        <Route exact path={navLink.error404}>
          <h1 className={s.title}>Ошибка 404</h1>
          <p>error</p>
        </Route>
        <Redirect to={navLink.error404}/>
      </Switch>
      <Footer/>
    </div>
  );
}


export default App;
